@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}

/* Anitmation (Add after) */
.item-enter {
  opacity: 0.01;
}

.item-enter-active {
  opacity: 1;
  -webkit-transition: opacity 1000ms ease-in;
  transition: opacity 1000ms ease-in;
}

.item-exit {
  opacity: 1;
}

.item-exit-active {
  opacity: 0;
  -webkit-transition: opacity 1000ms ease-in;
  transition: opacity 1000ms ease-in;
}

/* MOBILE STYLING */

@media (max-width: 1024px) {
  .navbar-links {
    display: flex;
  }
}

@media (max-width: 720px) {
  body {
    overflow-x: hidden;
  }
  .hide-sm {
    display: none;
  }
  .navbar-links {
    position: fixed;
    right: 0px;
    height: 92vh;
    top: 8vh;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    display: none;
  }
  .navbar-links li {
    opacity: 0;
    display: none;
  }
  .navbar-links ul {
    display: none;
  }

  .burger {
    display: block !important;
    padding-right: 19px;
    cursor: pointer;
  }
  .landing-page {
    margin-bottom: 40px;
  }
  .modal-overlay {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1040;
    width: 100vw;
    height: 100vh;
    background-color: #000;
    opacity: 0.5;
  }

  .modal-wrapper {
    position: fixed;
    top: 0;
    right: ;
    z-index: 1050;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    outline: 0;
  }

  .modal {
    z-index: 100;
    background-image: url(/static/media/adrien-olichon-RCAhiGJsUUE-unsplash.ec45de49.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 3px;
    max-width: 180px;
    height: 100vh;
    padding: 2rem;
    float: right;
  }

  .modal-header {
    display: flex;
    justify-content: flex-end;
  }

  .modal-burger-button {
    color: #fd4e00;
    background-color: #000;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1;
    width: 25px;
    color: #fd4e00;
    cursor: pointer;
    border-radius: 5px;
    margin-bottom: 10px;
  }

  .burger-navbar {
    display: flex;
  }
  .burger-navbar-links {
    justify-content: center;
    list-style-type: none;
  }
  .workouts {
    min-height: 530px;
    overflow-y: scroll;
  }
}

/* DESKTOP STYLING */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Montserrat;
}

body {
  background-image: url(/static/media/adrien-olichon-RCAhiGJsUUE-unsplash.ec45de49.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  color: white;
  height: 80vh;
}

.containerPage {
  display: block;
}

.landing-page {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
