.form-container {
  max-width: 500px;
  margin: 2rem auto;
  overflow: hidden;
  padding: 0 2rem;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.register-form {
  margin-top: 40px;
  align-items: center;
}

.form-group {
