}

.form-group input {
  margin: 0.2rem 0;
}
input[type='text'],
input[type='email'],
input[type='password'],
input[type='password2'],
select,
textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  border: 1px solid #ccc;
  font-size: 16px;
  letter-spacing: 2px;
  font-family: Muli;
  width: 18rem;
}

:root {
  --primary-color: #003699;
  --dark-color: #333333;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}
.alert {
  padding: 0.7rem;
  margin: 1rem 0;
  opacity: 0.9;
  text-align: center;
  background: #f4f4f4;
  background: var(--light-color);
  color: #333;
}
.alert-light {
  background: #f4f4f4;
  background: var(--light-color);
  color: #333;
}
.alert-dark {
  background: #333333;
  background: var(--dark-color);
  color: #fff;
}
.alert-danger {
  background: #dc3545;
  background: var(--danger-color);
  color: #fff;
}
.alert-success {
  background: #28a745;
  background: var(--success-color);
  color: #fff;
}
.alert-white {
  background: #fff;
  color: #333;
  border: #ccc solid 1px;
}
.alert-primary {
  background: #003699;
  background: var(--primary-color);
  color: #fff;
}

